import {
  PoTableColumn,
  PoButtonGroupItem,
  PoI18nService,
} from "@po-ui/ng-components";
import { Component, OnDestroy, OnInit } from "@angular/core";
import { BTService } from "../service/bt.service";
import { of, Subject, takeUntil } from "rxjs";
import { BtLiterals } from "../../monitor/models/bt-literals";
import { Router } from "@angular/router";

@Component({
  selector: "app-bt",
  templateUrl: "./bt.component.html",
  styleUrls: ["./bt.component.css"],
})
export class BTComponent implements OnInit, OnDestroy {
  sIconButtonSelected: string = "po-icon-last-page";
  sIconButtonDash: string = "";
  sIconButtonTopBT: string = "";
  sIconButtonBTsnap: string = this.sIconButtonSelected;

  bDash: boolean = false;
  btopBT: boolean = false;
  bBTsnap: boolean = true;

  buttonsBT: Array<PoButtonGroupItem>;
  columnsBT: Array<PoTableColumn>;
  itemsBT: Array<any> = [];
  itemsBTaux: Array<any> = [];

  /*-- Unsubscribe --*/
  destroy$: Subject<boolean> = new Subject<boolean>();

  /*-- i18n --*/
  literals: BtLiterals;

  constructor(
    private btService: BTService,
    private i18nService: PoI18nService,
    private router: Router
  ) {
    this.i18nService
      .getLiterals({ context: "bt", language: this.i18nService.getLanguage() })
      .pipe(takeUntil(this.destroy$))
      .subscribe((literals: BtLiterals) => (this.literals = literals));
  }

  ngOnInit(): void {
    this.buttonsBT = [
      {
        label: this.literals.buttonsBtLabelAll,
        icon: "po-icon-grid",
        action: this.filterBTs.bind(this),
        tooltip: this.literals.buttonsBtTooltipAll,
        selected: true,
      },
      {
        label: this.literals.buttonsBtLabelSlowOrError,
        icon: "po-icon-exclamation",
        action: this.filterBTs.bind(this),
        tooltip: this.literals.buttonsBtTooltipSlowOrError,
      },
    ];

    this.columnsBT = [
      {
        property: "status",
        label: this.literals.columnsBtLabelStatus,
        type: "label",
        width: "8%",
        labels: [
          {
            value: "ok",
            color: "color-11",
            label: "",
            tooltip: this.literals.columnsBtTooltipStatusOk,
          },
          {
            value: "warn",
            color: "color-08",
            label: "",
            tooltip: this.literals.columnsBtTooltipStatusSlow,
          },
          {
            value: "error",
            color: "color-07",
            label: "",
            tooltip: this.literals.columnsBtTooltipStatusError,
          },
        ],
      },
      {
        property: "date",
        label: this.literals.columnsBtLabelDate,
        type: "string",
      },
      {
        property: "time",
        label: this.literals.columnsBtLabelTime,
        type: "time",
      },
      {
        property: "exec",
        label: this.literals.columnsBtLabelExecInTime,
        type: "number",
      },
      { 
        property: "bt", 
        label: this.literals.columnsBtLabelBt,
        type: "link",
        action: this.goToDetails.bind(this)
      },
      {
        property: "node",
        label: this.literals.columnsBtLabelNode
      },
    ];

    this.getListOfBT();
  }

  clickMenu(sType: string) {
    this.sIconButtonDash = "";
    this.sIconButtonTopBT = "";
    this.sIconButtonBTsnap = "";
    this.bDash = false;
    this.btopBT = false;
    this.bBTsnap = false;
    switch (sType) {
      case "dash":
        this.sIconButtonDash = this.sIconButtonSelected;
        this.bDash = true;
        break;
      case "topBT":
        this.sIconButtonTopBT = this.sIconButtonSelected;
        this.btopBT = true;
        break;
      case "BTsnap":
        this.sIconButtonBTsnap = this.sIconButtonSelected;
        this.bBTsnap = true;
        break;
    }
  }

  getListOfBT(): void {
    this.itemsBT = [];
    this.btService
      .getBTList()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: items => {
          this.itemsBT = items.list;
          this.itemsBTaux = items.list;
        },
        error: e => {
          console.error(e);
          return of();
        },
      });
  }

  filterBTs(button) {
    if (button.label === "All") this.itemsBT = this.itemsBTaux;
    else this.itemsBT = this.itemsBT.filter(item => item.status != "ok");
  }

  goToDetails(col, row) {
    this.router.navigate(["bt/call-graph", JSON.stringify(row)]);
  }

  ngOnDestroy(): void {
    this.destroy$.next(false);
    this.destroy$.unsubscribe();
  }
}
