import { Observable, catchError, throwError, take, tap } from "rxjs";
import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Host } from "../../shared/components/host.component";
import { TreeNode } from "primeng/api";

const urlBaseManager: string = "tlpp/gui/";
const urlBTlist: string = "bt/list";
const urlCGlist: string = "cg/list";
const urlEClist: string = "ec/list";

@Injectable({
  providedIn: "root",
})
export class BTService {
  private readonly location: Location;

  constructor(private http: HttpClient, private host: Host) {}

  getBTList(): Observable<any> {
    let sURL = urlBaseManager + urlBTlist;
    return this.http.get<any>(`${this.host.setUrlService(sURL)}`).pipe(
      take(1),
      catchError(err => {
        console.log(err);
        return throwError(() => err);
      })
    );
  }
  getCallGraphs(uuid: string, bt_id: string) {
    let sURL = urlBaseManager + urlCGlist + '?uuid=' + uuid + '&btid=' + bt_id ;
    return this.http.get<TreeNode[]>(`${this.host.setUrlService(sURL)}`).pipe(
      take(1),
      catchError(err => {
        console.log(err);
        return throwError(() => err);
      })
    );
  }
  getExitCalls(uuid: string, bt_id: string, cg_id: string) {
    let sURL = urlBaseManager + urlEClist + '?uuid=' + uuid + '&btid=' + bt_id + '&cgid=' + cg_id;
    return this.http.get<any>(`${this.host.setUrlService(sURL)}`).pipe(
      take(1),
      catchError(err => {
        console.log(err);
        return throwError(() => err);
      })
    );
  }
}
