import { Component, OnDestroy, OnInit, ViewChild } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { CallGraphParameters } from "../models/call-graph-parameters";
import { TreeNode } from "primeng/api";
import { BTService } from "../service/bt.service";
import { PoPageSlideComponent } from "@po-ui/ng-components";
import { Chart } from "chart.js";
import ChartDataLabels from "chartjs-plugin-datalabels";

Chart.register(ChartDataLabels);

@Component({
  selector: "app-call-graph",
  templateUrl: "./call-graph.component.html",
  styleUrls: ["./call-graph.component.css"],
})
export class CallGraphComponent implements OnInit, OnDestroy {
  // Primeng
  selectedNode: TreeNode; /*"strictPropertyInitialization": false tsconfig.json*/
  tableData: TreeNode[] = [];
  treeCols: any[] = [];
  totalRecords: number = 0;
  loading: boolean = false;

  subscription: Subscription[] = [];
  routeParams: CallGraphParameters;
  executionTime: string;
  node: string;
  uuid: string;
  bt_id: string;
  timestamp: string;
  search = {};

  // exit call
  @ViewChild(PoPageSlideComponent, { static: true })
  pageSlide: PoPageSlideComponent;
  exitCallTitle: string;
  exitCallName: string;
  exitCallType: string;
  exitCallClass: string;
  exitCallMethod: string;
  exitCallLine: string;
  exitCallTableData: any[];
  exitCallTableCols: any[];
  exitCallDetailsTitle: string;
  exitCallDetailsTime: string;
  exitCallDetailsFrom: string;
  exitCallDetailsTo: string;
  exitCallDetailsDetails: string;
  exitCallPropertiesTitle: string;
  exitCallPropertiesDetails: string;
  // chartjs
  basicData: any;
  options = {
    plugins: {
      legend: { display: false },
      // ChartDataLabels,
      datalabels: {
        formatter: function (value: any, context: any) {
          return Math.round(value) + "%";
        },
      },
    },
    elements: {
      bar: {
        borderWidth: 2,
        borderRadius: 15,
      },
    },
    maintainAspectRatio: false,
    indexAxis: "y",
    scales: {
      y: {
        stacked: true,
        grid: {
          display: false,
        },
        position: "left",
      },
      x: {
        display: false,
        min: 0,
        max: 100,
        grid: {
          display: false,
        },
      },
    },
  };
  constructor(private route: ActivatedRoute, private btService: BTService) {}

  ngOnInit(): void {
    this.loading = true;
    this.subscription.push(
      this.route.params.subscribe(params => {
        this.routeParams = JSON.parse(params.btreference);
        this.node = this.routeParams.node;
        this.uuid = this.routeParams.uuid;
        this.bt_id = this.routeParams.bt_id;
        this.executionTime = `${this.routeParams.exec} ms`;
        this.timestamp = `${this.routeParams.date} ${this.routeParams.time}`;
      })
    );
    this.treeCols = [
      { field: "name", header: "Name" },
      { field: "execution", header: "Execution" },
      { field: "call", header: "Call From" },
      { field: "time", header: "Time(ms)" },
      { field: "percent", header: "Percent %" },
      { field: "exit", header: "Exit Calls" },
    ];
  }

  loadNodes(event: any) {
    this.subscription.push(
      this.btService.getCallGraphs(this.uuid,this.bt_id).subscribe(cg => {
        this.tableData = cg['list'];
        this.totalRecords = this.tableData.length;
        this.loading = false;
      })
    );
  }
  change(event: any) {
    console.log(event);
  }
  nodeSelect(event: any) {
    this.getExitCallsData(event);
    this.pageSlide.open();
  }

  getExitCallsData(event: any) {
    this.exitCallTitle = event.node.data['exec'];
    this.exitCallName = event.node.data['class'] ? event.node.data['class'] + ':' : '';
    this.exitCallName += event.node.data['method'] + '()';
    this.exitCallType = event.node.data['class'] ? 'CLASS' : 'FUNCTION';
    this.exitCallClass = event.node.data['class'];
    this.exitCallMethod = event.node.data['method'];
    this.exitCallLine = event.node.data['execL'];
    this.exitCallTableCols = [
      { field: "type", header: "Type" },
      { field: "year", header: "Details" },
      { field: "time", header: "Time(ms)" },
      { field: "percent", header: "%Time" },
      { field: "to", header: "To" },
    ];
    this.subscription.push(
      this.btService.getExitCalls(this.uuid,this.bt_id,event.node.data['cg_id']).subscribe(ec => {
        console.log(ec);
        this.exitCallTableData = ec['list'];
      })
    );
    // this.exitCallDetailsTitle = "DB Exit Calls";
    // this.exitCallDetailsTime = "1ms";
    // this.exitCallDetailsFrom = "Appserver01Tier";
    // this.exitCallDetailsTo = "DBAccess-127.0.0.1-7890-P12.1.16-TOTVSDBAccess";
    // this.exitCallDetailsDetails =
    //   "bt_id[21]-ecid[41]-msg_id[0]-msg_name[testestetstetsttettetetstststt]";
    // this.exitCallPropertiesTitle = "Properties";
    // this.exitCallPropertiesDetails = "1559070875463855";
    // this.basicData = {
    //   labels: ["Self Time", "Total Time"],
    //   datasets: [
    //     {
    //       data: [87, 50],
    //       fill: false,
    //       backgroundColor: "rgba(54,162,235,0.7)",
    //       datalabels: {
    //         color: "gray",
    //         font: {
    //           size: "10",
    //         },
    //       },
    //     },
    //     {
    //       data: [100, 100],
    //       fill: true,
    //       backgroundColor: "rgba(220,218,218,0.7)",
    //       datalabels: {
    //         color: "#0000",
    //       },
    //     },
    //   ],
    // };
  }

  ngOnDestroy(): void {
    this.subscription.forEach(subscriptions => {
      subscriptions.unsubscribe();
    });
  }
  public closePage() {
    this.pageSlide.close();
  }
}
