import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

import { AuthGuard } from "../guards/auth.guard";
import { BtBaseComponent } from "./bt-base/bt-base.component";
import { BTComponent } from "./bt/bt.component";
import { CallGraphComponent } from "./call-graph/call-graph.component";

const routes: Routes = [
  {
    path: "",
    component: BtBaseComponent,
    children: [
      { path: "bt", component: BTComponent, canActivate: [AuthGuard] },
      {
        path: "call-graph/:btreference",
        component: CallGraphComponent,
        canActivate: [AuthGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BtRoutingModule {}
